import { inject } from 'aurelia-framework';
import { Router } from "aurelia-router";
import { AppConfig } from '../../../config/appConfig';

@inject(Router, AppConfig)
export class TechRequests {
    title = "Tech Staff Client Assignments"

    constructor(router, config) {
        this.router = router;
        this.config = config;
    }

    attached() {
        $(".nav a").on("click", function () {
            $(".nav").find(".active").removeClass("active");
            $(this).parent().addClass("active");
        });
    }

    activate() {
        this.config.getConfig(true);
    }

    configureRouter(config, router) {
        config.map([
            {
                route: ['', 'assignments'],
                moduleId: PLATFORM.moduleName('./assignments'),
                settings: { auth: true, roles: [] },
                nav: true,
                name: 'assignments',
                title: "Assignments"
            },
            {
                route: 'createRequest',
                moduleId: PLATFORM.moduleName('./createRequest'),
                settings: { auth: true, roles: [] },
                nav: true,
                name: 'createRequest',
                title: 'Create Request'
            },
            // {
            //     route: 'clientRequestsAnalytics',
            //     moduleId: PLATFORM.moduleName('../../analytics/clientRequests'),
            //     settings: { auth: true, roles: [] },
            //     nav: true,
            //     name: 'clientRequests',
            //     title: 'Client Requests Analytics'
            // },
            {
                route: 'clientRequestsArchive',
                moduleId: PLATFORM.moduleName('./archiveClientRequests'),
                settings: { auth: true, roles: [] },
                nav: true,
                name: 'archiveClientRequests',
                title: 'Client Requests Archive'
            },
            {
                route: 'viewUserRequests',
                moduleId: PLATFORM.moduleName('./viewUserRequests'),
                settings: { auth: true, roles: [] },
                nav: true,
                name: 'viewUserRequests',
                title: 'View User Requests'
            },
            // {
            //     route: 'apjRequests',
            //     moduleId: PLATFORM.moduleName('./apjRequests'),
            //     settings: { auth: true, roles: [] },
            //     nav: true,
            //     name: 'apjRequests',
            //     title: 'APJ Requests'
            // }
        ]);

        this.router = router;
    }

}